      msg = msg.replace(/[|]*\n/, ''); //去除行尾空格
      msg = msg.replace(/&nbsp;/ig, ' '); //去掉npsp
      msg = msg.replace(/&amp;nbsp;/ig, ' '); //去掉npsp
      msg = msg.replace(/[\r\n]/g, " "); //去掉回车换行
      msg = msg.replace(/\s+/g, " "); //去掉回车换行
      return msg;
